@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap'); /* font-family: 'IBM Plex Mono', monospace; */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'IBM Plex Mono', monospace;
}

.hr {
    height: 1px;
    background-color: #75B2D6;
}

.Input {
    position: relative;
    height: 9vh;
    background-color: #192A32;
    margin-bottom: 2vh;
    border-radius: 2vh;
}
.Input.indexLogin {
    width: calc(100px + 40%);
}
.Input > .placeholder {
    position: absolute;
    top: 50%;
    left: 2vh;
    transform: translateY(-50%);
    font-size: 2.5vh;
    pointer-events: none;
}
.Input > .eye {
    position: absolute;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    right: 1.5vh;
    height: 1.5vh;
}
.Input > input {
    height: 100%;
    width: 100%;
    background-color: transparent;
    border: none;
    outline: none;
    font-size: 2.5vh;
    padding: 2vh;
    color: white;
}

.btn {
    padding: 1vh 2vh;
    font-size: 2vh;
    border-radius: 1.5vh;
    cursor: pointer;
}

.SearchInput {
    position: relative;
    height: 5vh;
    width: 33%;
    background-color: transparent;
    margin-bottom: 2vh;
    border-bottom: 1px solid #75B2D6;
}
.SearchInput > .searchIcon {
    position: absolute;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    left: 0px;
    height: 2vh;
    margin-top: 0.2vh;
}
.SearchInput > input {
    height: 100%;
    width: 100%;
    background-color: transparent;
    border: none;
    outline: none;
    font-size: 2.5vh;
    padding-left: 3vh;
    color: white;
}
.SearchInput > .placeholder {
    position: absolute;
    top: 50%;
    left: 3vh;
    transform: translateY(-50%);
    font-size: 2.5vh;
    pointer-events: none;
}

@media screen and (max-width: 1160px) {
    .Input.indexLogin {
        width: 95%;
    }
}